<template>
  <div class="bodyImg">
    <div class="container mt-4 justify-content-center">
      <loading
        v-if="getIsLoading"
        :is-full-page="true"
        :loader="'dots'"
        :active="true"
        :color="'#008ec6'"
        :height="80"
        :width="80"
        :background-color="'#999999'"
      ></loading>
      <h1>Your Flights:</h1>
      <div class="d-flex flex-wrap">
        <zoom-center-transition
          :duration="1500"
          v-for="(flight,index) in getFilteredFlights"
          :key="index"
        >
          <div v-show="getShow">
            <card
              :flight="flight"
              :isMyFlights="true"
              :departCity="flight.departCity.toUpperCase()"
              :returnCity="flight.returnCity.toUpperCase()"
            ></card>
          </div>
        </zoom-center-transition>
      </div>
      <div class="container" v-if="!getshowButtons || getFlights.length === 0? false : true ">
        <div class="d-flex justify-content-around">
          <button @click="decrementPage">
            <prev-button class="mybtn"></prev-button>
          </button>
          <button @click="incrementPage">
            <next-button class="mybtn"></next-button>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Card from "../components/Card";
import { ZoomCenterTransition } from "vue2-transitions";
import NextButton from "../components/svg/Next";
import PrevButton from "../components/svg/Prev";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
export default {
  components: {
    Card,
    //SlideYUpTransition,
    ZoomCenterTransition,
    NextButton,
    PrevButton,
    Loading
  },
  watch: {},
  data() {
    return {
      show: false,
      showButtons: false,
      flights: this.$store.getters.getMyFlights,
      pageIndex: 0,
      itemsPerPage: 3,
      flightsLength: this.$store.getters.getMyFlights.length,
      isLoading: false
    };
  },
  computed: {
    getFlights() {
      return this.$store.getters.getMyFlights;
    },
    getIsLoading() {
      return this.isLoading;
    },
    getShow() {
      return this.show;
    },
    getPageIndex: {
      get() {
        return this.pageIndex;
      }
    },
    getFilteredFlights() {
      return this.getFlights.slice(this.getPageIndex, this.getPageIndex + 3);
    },
    getDepartCity() {
      return this.departCity;
    },
    getReturnCity() {
      return this.returnCity;
    },
    getshowButtons: {
      get() {
        return this.showButtons;
      },
      set(arg) {
        this.showButtons = arg;
      }
    }
  },
  methods: {
    incrementPage() {
      this.pageIndex += this.itemsPerPage;
      if (this.pageIndex > this.flightsLength) this.pageIndex = 0;
      this.pageIndex = this.pageIndex % this.flightsLength;
      this.show = false;
      setTimeout(() => {
        this.show = true;
      }, 100);
    },
    decrementPage() {
      this.pageIndex -= this.itemsPerPage;
      this.pageIndex = Math.abs(this.pageIndex);
      if (this.pageIndex > this.flightsLength) this.pageIndex = 0;
      this.pageIndex = this.pageIndex % this.flightsLength;
      this.show = false;
      setTimeout(() => {
        this.show = true;
      }, 100);
    }
  },
  created() {
    this.isLoading = true;
    this.$store.dispatch("fetchMyFlights").then(() => {
      setTimeout(() => {
        this.flights = this.$store.getters.getMyFlights;
        this.flightsLength = this.$store.getters.getMyFlights.length;
        this.isLoading = false;
        setTimeout(() => {
          this.show = true;
          this.getshowButtons = true;
        }, 100);
      }, 1500);
    });
  },
  beforeRouteLeave(to, from, next) {
    // ...
    this.show = false;
    this.getshowButtons = false;
    setTimeout(() => {
      next();
    }, 1500);
  }
};
</script>
<style scoped>
@import url("https://fonts.googleapis.com/css?family=Notable");
@import url("https://fonts.googleapis.com/css?family=Lobster");
.bodyImg {
  background: url("../assets/images/FBe0oc3.jpg") no-repeat fixed;
  background-size: cover;
  height: calc(100vh - 80px);
  overflow: auto;
}
.mybtn:hover {
  cursor: pointer;
  -moz-box-shadow: 0 0 20px #fff;
  -webkit-box-shadow: 0 0 20px #fff;
  box-shadow: 0px 0px 20px #fff;
  border-radius: 40px;
}
button {
  background: none;
  border-style: none;
  outline: none;
}
.bodyImg {
  background: url("../assets/images/FBe0oc3.jpg") no-repeat fixed;
  background-size: cover;
  height: calc(100vh - 80px);
  overflow: auto;
}
h1 {
  color: white;
  font-family: "Lobster";
  color: white;
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000,
    -1px 1px 0 #000, 1px 1px 0 #000;
}
</style>