<template>
  <div class="bodyImg">
    <loading v-if="getIsLoading" :is-full-page="true" :loader="'dots'" :active="true" :color="'#008ec6'" :height="80" :width="80" :background-color="'#999999'"></loading>
    <fade-transition :duration="500">
      <div v-show="showFrom" class="container">
        <div class="d-flex justify-content-center align-items-center mt-4">
          <div class="border border-dark">
            <div class="d-flex flex-column justify-content-center align-items-center">
              <div class="text-center title font-weight-bolder mt-4 mb-4">LOGIN</div>
              <form @submit="onSubmit">
                <div class="form-group font-weight-bold">
                  <label for="email">Email</label>
                  <input
                    type="email"
                    v-model="$v.email.$model"
                    class="form-control"
                    :class="$v.email.email && $v.email.required ? 'is-valid' : 'is-invalid'"
                    id="email"
                    aria-describedby="emailHelp"
                  />
                  <small id="emailHelp" class="form-text text-muted">Your email must be valid.</small>
                </div>
                <div class="form-group font-weight-bold">
                  <label for="password">Password</label>
                  <input
                    v-model="$v.password.$model"
                    type="password"
                    class="form-control"
                    :class="$v.password.required && $v.password.minLength && $v.password.alphaNum ? 'is-valid' : 'is-invalid'"
                    id="password"
                  />
                  <small
                    id="passwordHelp"
                    class="form-text text-muted"
                  >Your password must contain at least 8 characters and numbers.</small>
                </div>
                <div class="d-flex align-items-stretch">
                  <button
                    type="submit"
                    class="btn mb-4"
                    :class="checkFormValidity? 'btn-dark': 'btn-success' "
                  >Login</button>
                  <div class="p-2">
                    Forgot your password?
                    <a href="/#/reset">Reset</a>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </fade-transition>
  </div>
</template>
<script>
import { email, required, minLength, alphaNum } from "vuelidate/lib/validators";
import { FadeTransition } from "vue2-transitions";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
export default {
  components: {
    FadeTransition,
    Loading
    
  },
  computed: {
    getErrorHandler() {
      return this.errorHandler;
    },
    checkFormValidity() {
      if (this.$v.$invalid) {
        console.log(this.$v.$invalid);
        return true;
      }
      return false;
    },
    getIsLoading(){
      return this.isLoading;
    }
  },
  data() {
    return {
      email: "",
      password: "",
      showFrom: false,
      isLoading: false
    };
  },
  validations: {
    email: {
      required,
      email
    },
    password: {
      required,
      minLength: minLength(8),
      alphaNum
    }
  },
  methods: {
    onSubmit(e) {
      this.isLoading = true;
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.isLoading = false;
        this.toastHandling('Please make sure you fill the form correctly','info')
      } else {
        const user = {
          email: this.email,
          password: this.password
        };
        this.$store.dispatch("submitLogin", user).then(error => {
          let message = error.message;
          let type = error.type;
          this.isLoading = false;
          this.toastHandling(message, type);
          if(type === "success"){
            setTimeout(()=>{
                 this.$router.push('/')
            },1000)
         
          }
        });
      }
      
      e.preventDefault();
    },
    toastHandling(message, type) {
      this.$toasted.show(message, {
        position: "top-center",
        duration: "5000",
        type: type,
        theme: "outline",
        className: "mt-4"
      });
    }
  },
  created() {
    setTimeout(() => {
      this.showFrom = true;
    }, 500);
  },
  beforeRouteLeave(to, from, next) {
    this.showFrom = false;
    setTimeout(() => {
      next();
    }, 1500);
  }
};
</script>
<style scoped>
.bodyImg {
  background: url("../../assets/images/signup.jpg") no-repeat 0% 80% fixed;
  background-size: cover;
  height: calc(100vh - 80px);
  overflow: auto;
}
.border {
  background: white;
  border-radius: 20px;
  border-color: black;
}
.title {
  font-size: 1.5em;
}
.border {
  width: 25rem;
  opacity: 80%;
  transition: 0.5s;
}
.border:hover {
  width: 25rem;
  opacity: 100%;
}
</style>