<template>
  <div class="bodyImg">
    <div class="container mt-4 justify-content-center">
      <loading
        v-if="getIsLoading"
        :is-full-page="true"
        :loader="'dots'"
        :active="true"
        :color="'#008ec6'"
        :height="80"
        :width="80"
        :background-color="'#999999'"
      ></loading>
      <div class="d-flex flex-wrap">
        <zoom-center-transition
          :duration="750"
          v-for="(flight,index) in getFilteredFlights"
          :key="index"
        >
          <div v-show="getShow">
            <card
              :flight="flight"
              :isMyFlights="false"
              :departCity="departCity.toUpperCase()"
              :returnCity="returnCity.toUpperCase()"
            ></card>
          </div>
        </zoom-center-transition>
      </div>
      <div class="container" v-if="getshowButtons">
        <div class="d-flex justify-content-around">
          <button @click="decrementPage">
            <prev-button class="mybtn"></prev-button>
          </button>
          <button @click="incrementPage">
            <next-button class="mybtn"></next-button>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Card from "../components/Card";
import { ZoomCenterTransition } from "vue2-transitions";
import NextButton from "../components/svg/Next";
import PrevButton from "../components/svg/Prev";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
export default {
  components: {
    Card,
    //SlideYUpTransition,
    ZoomCenterTransition,
    NextButton,
    PrevButton,
    Loading
  },
  data() {
    return {
      show: false,
      showButtons: false,
      flights: this.$store.getters.getFlights,
      departCity: this.$store.getters.getDepartCity,
      returnCity: this.$store.getters.getReturnCity,
      pageIndex: 0,
      itemsPerPage: 3,
      flightsLength: this.$store.getters.getFlights.length,
      isLoading: false
    };
  },
  computed: {
    getIsLoading() {
      return this.isLoading;
    },
    getShow() {
      return this.show;
    },
    getFlights() {
      return this.flights;
    },
    getPageIndex: {
      get() {
        return this.pageIndex;
      }
    },
    getFilteredFlights() {
      return this.getFlights.slice(this.getPageIndex, this.getPageIndex + 3);
    },
    getDepartCity() {
      return this.departCity;
    },
    getReturnCity() {
      return this.returnCity;
    },
    getshowButtons: {
      get() {
        return this.showButtons;
      },
      set(arg) {
        this.showButtons = arg;
      }
    }
  },
  methods: {
    incrementPage() {
      this.pageIndex += this.itemsPerPage;
      if (this.pageIndex > this.flightsLength) this.pageIndex = 0;
      this.pageIndex = this.pageIndex % this.flightsLength;
      this.show = false;
      setTimeout(() => {
        this.show = true;
      }, 100);
    },
    decrementPage() {
      this.pageIndex -= this.itemsPerPage;
      this.pageIndex = Math.abs(this.pageIndex);
      if (this.pageIndex > this.flightsLength) this.pageIndex = 0;
      this.pageIndex = this.pageIndex % this.flightsLength;
      this.show = false;
      setTimeout(() => {
        this.show = true;
      }, 100);
    }
  },
  mounted() {
    setTimeout(() => {
      if (this.flightsLength !== 0) {
        this.show = true;
        this.getshowButtons = true;
      } else {
        this.$toasted.show("No flights available due to covid-19 , please try another destination.", {
          position: "top-center",
          duration: "5000",
          type: 'error',
          theme: "outline",
          className: "mt-4"
        });
      }
    }, 1500);
  },
  beforeRouteLeave(to, from, next) {
    // ...

    this.show = false;
    this.getshowButtons = false;
    setTimeout(() => {
      next();
    }, 1500);
  }
};
</script>
<style scoped>
.bodyImg {
  background: url("../assets/images/FBe0oc3.jpg") no-repeat fixed;
  background-size: cover;
  height: calc(100vh - 80px);
  overflow: auto;
}
.mybtn:hover {
  cursor: pointer;
  -moz-box-shadow: 0 0 20px #fff;
  -webkit-box-shadow: 0 0 20px #fff;
  box-shadow: 0px 0px 20px #fff;
  border-radius: 40px;
}
button {
  background: none;
  border-style: none;
  outline: none;
}
.bodyImg {
  background: url("../assets/images/FBe0oc3.jpg") no-repeat fixed;
  background-size: cover;
  height: calc(100vh - 80px);
  overflow: auto;
}
</style>