<template>
  <div class="p-2">
    <loading
      v-if="getIsLoading"
      :is-full-page="true"
      :loader="'dots'"
      :active="true"
      :color="'#008ec6'"
      :height="80"
      :width="80"
      :background-color="'#999999'"
    ></loading>
    <div class="card border-dark mb-3 fill">
      <div class="card-header font-weight-bold text-center">
        <div>
          {{departCity}}
          <between-icon></between-icon>
          {{returnCity}}
        </div>
        <div>
          {{flight.depart.departure.fullDateTime.substring(0,10)}} - {{flight.return.departure.fullDateTime.substring(0,10)}}
        </div>
      </div>
      <div class="card-body text-dark">
        <div class="d-flex justify-content-between align-items-center">
          <div class="d-flex">
            <img :src="flight.depart.imgURL" width="40" height="40" alt />
          </div>
          <div class="d-flex flex-column justify-content-center">
            <div class="font-weight-bold">{{flight.depart.departure.time}}</div>
            <div class="font-italic">{{flight.depart.departure.iataCode}}</div>
          </div>
          <div class="d-flex flex-column">
            <div>
              <hr-flight-icon></hr-flight-icon>
            </div>
            <div class="green font-italic">Direct</div>
          </div>
          <div class="d-flex flex-column">
            <div class="font-weight-bold">{{flight.depart.arrival.time}}</div>
            <div class="font-italic">{{flight.depart.arrival.iataCode}}</div>
          </div>
        </div>
      </div>
      <div class="card-body text-dark">
        <div class="d-flex justify-content-between align-items-center">
          <div class="d-flex">
            <img :src="flight.return.imgURL" width="40" height="40" alt />
          </div>
          <div class="d-flex flex-column justify-content-center">
            <div class="font-weight-bold">{{flight.return.departure.time}}</div>
            <div class="font-italic">{{flight.return.departure.iataCode}}</div>
          </div>
          <div class="d-flex flex-column">
            <div>
              <hr-flight-icon></hr-flight-icon>
            </div>
            <div class="green font-italic">Direct</div>
          </div>
          <div class="d-flex flex-column">
            <div class="font-weight-bold">{{flight.return.arrival.time}}</div>
            <div class="font-italic">{{flight.return.arrival.iataCode}}</div>
          </div>
        </div>
        <hr />
        <div class="card-body text-dark">
          <div class="d-flex justify-content-between align-items-center">
            <div class="font-weight-light font-italic" v-if="flight.pricePerAdult.total && flight.pricePerAdult.total > 1">
              Price per person
              <span>{{flight.price.total / flight.pricePerAdult.total}} {{flight.price.symbol}}</span>
            </div>
            <div class="font-weight-bold">
              Total
              <span>{{flight.price.total}}{{flight.price.symbol}}</span>
            </div>
            <div>
              <button
                v-if="isMyFlights === false"
                @click="addFlight(flight,departCity,returnCity)"
                class="btn btn-primary"
              >Add</button>
              <button v-else @click="deleteFlight(flight)" class="btn btn-danger">Remove</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import betweenIcon from "../components/svg/betweenIcon";
import hrFlightIcon from "../components/svg/hrFlight";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
export default {
  props: ["flight", "departCity", "returnCity", "isMyFlights"],
  components: {
    betweenIcon,
    hrFlightIcon,
    Loading
  },
  data() {
    return {
      isLoading: false
    };
  },
  computed: {
    getAuth() {
      return this.$store.getters.getIsAuth;
    },
    getIsLoading() {
      return this.isLoading;
    }
  },
  created() {
    console.log(this.isMyFlights + " abc");
  },
  methods: {
    addFlight(flight, departCity, returnCity) {
      if (!this.getAuth) {
        this.toastHandling("Please login or signup first", "info");
        return;
      }
      const arr = [flight, departCity, returnCity];
      this.isLoading = true;
      this.$store.dispatch("addFlight", arr).then(result => {
        this.isLoading = false;
        this.toastHandling(result.message, result.type);
      });
    },
    deleteFlight(flight) {
      if (!this.getAuth) {
        this.toastHandling("Please login or signup first", "info");
        return;
      }
      this.isLoading = true;
      this.$store.dispatch("deleteFlights", flight).then(result => {
        this.isLoading = false;
        this.toastHandling(result.message, result.type);
      });
    },
    toastHandling(message, type) {
      this.$toasted.show(message, {
        position: "top-center",
        duration: "5000",
        type: type,
        theme: "outline",
        className: "mt-4"
      });
    }
  }
};
</script>
<style scoped>
.fill {
  border-radius: 20px;
  background-color: rgb(255, 255, 255, 0.6) !important;
}
.card {
  width: 22rem;
  transition: 0.5s;
}
.card:hover {
  max-width: 22rem;
  background-color: white !important;
}
.green {
  color: green;
}
</style>